import { Component, OnInit, ViewChild } from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActionDialogComponent } from "../action-dialog/action-dialog.component";
import { MatDialog, MatDialogRef } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { MessageConstant } from "app/shared/utils/message.constant";
import { MatTableFilter } from "mat-table-filter";
import { ViewEncapsulation } from "@angular/core";
import { fuseAnimations } from "@fuse/animations";
import { FuseConfirmDialogComponent } from "@fuse/components/confirm-dialog/confirm-dialog.component";
import * as XLSX from "xlsx";
import { jsPDF } from "jspdf";
import "jspdf-autotable";
import { map, startWith } from "rxjs/operators";
import { Validators, FormBuilder, FormGroup } from "@angular/forms";
import { Observable } from "rxjs";
import { RfidService } from "app/shared/services/rfid/rfid.service";
import { CpoService } from "app/shared/services/cpo/cpo.service";

export class RFID {
    id: string;
    client_id: string;
    client_name: string;
    cpo_id: string;
    cpo_name: string;
    station_name: string;
    rfid_id: number;
    rf_id_no: string;
    balance: string;
}

@Component({
    selector: "app-list",
    templateUrl: "./list.component.html",
    styleUrls: ["./list.component.scss"],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations,
})
export class ListComponent implements OnInit {
    filterEntity: RFID;
    filterType: MatTableFilter;
    columns: any[];
    displayedColumns: string[];

    head = [
        [
            "Client Name",
            "CPO Name",
            "Station Name",
            "RFID",
            "Balance",
            "Action",
        ],
    ];
    public dataSource = new MatTableDataSource<any>([]);
    localStorage: any;
    @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
    @ViewChild(MatSort, { static: true }) sort: MatSort;
    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
    dialogRef: any;
    clientNameSearch = false;
    cpoSearch = false;
    stationNameSearch = false;
    rfidNoSearch = false;
    balanceSearch = false;
    chargAmountSearch = false;
    statusSearch = false;
    exportedData: any[];
    cposName: any;
    clientName: any;
    form: FormGroup;
    filteredOptionsCPO: Observable<any[]>;
    selectedCpo_id: any;
    localClient: any;
    rfidRecharge: any;
    constructor(
        public dialog: MatDialog,
        private snackBar: MatSnackBar,
        private fb: FormBuilder,
        private cpoService: CpoService,
        private rfidService: RfidService
    ) {
        this.form = this.fb.group({
            CPO: ["", Validators.required],
        });
    }

    ngOnInit() {
        this.localStorage = JSON.parse(localStorage.getItem("userDetails"));

        this.columns = [
            {
                field: "client_name",
            },
            {
                field: "cpo_name",
            },
            {
                field: "station_name",
            },
            {
                field: "rf_id_no",
            },
            {
                field: "balance",
            },
            {
                field: "action",
                show: ((this.localStorage.role_code !== "CPO") && (this.localStorage.role_code != "END"))
            },
        ];
        // Filter out the columns that should not be displayed
        this.displayedColumns = this.columns.filter(column => column.show !== false).map(column => column.field);

        this.getactivecposcw(this.localStorage.userId);
        this.filterEntity = new RFID();
        this.filterType = MatTableFilter.ANYWHERE;
    }

    getactivecposcw(id) {
        this.cpoService.getactivecposcwwithAll(id).subscribe(
            (data: any) => {
                console.log('data');
                console.log(data);
                
                this.cposName = data;
                if (this.localStorage.role_code != "END") {
                    if (this.cposName != null && this.cposName != undefined) {
                        const toSelect = this.cposName[0];
                        console.log("toselect");
                        console.log(toSelect);

                        this.selectedCpo_id = toSelect.id;
                        this.form.get("CPO").setValue(toSelect);

                        if (this.localStorage.role_code == "CPO") {
                            this.form.get("CPO").disable();
                        }

                        this.getRfidRecharge(this.selectedCpo_id);
                    }
                    this.filteredOptionsCPO =
                        this.form.controls.CPO.valueChanges.pipe(
                            startWith(""),
                            map((value) =>
                                typeof value === "string" ? value : value.name
                            ),
                            map((name) =>
                                name ? this._filterCPO(name) : this.cposName
                            )
                        );
                }
            },
            (err) => {}
        );
    }

    displayCPO(item: any): string {
        return item && item.name ? item.name : "";
    }

    private _filterCPO(name: string): any[] {
        const filterValue = name.toLowerCase();
        return this.cposName.filter((option) =>
            option.name.toLowerCase().includes(filterValue)
        );
    }

    onCPOChange(cpo_id) {
        this.selectedCpo_id = cpo_id;
        this.getRfidRecharge(cpo_id);
    }

    getRfidRecharge(cpo_id) {
        this.rfidService
            .getRechargeLogsbyCCSW(this.localStorage.userId, cpo_id)
            .subscribe(
                (resp: any) => {
                    if (resp.status) {
                        this.rfidRecharge = resp.data;
                        this.dataSource = new MatTableDataSource(
                            this.rfidRecharge
                        );
                        this.dataSource.paginator = this.paginator;
                        this.dataSource.sort = this.sort;
                    } else {
                        this.rfidRecharge = [];
                        this.dataSource = new MatTableDataSource(
                            this.rfidRecharge
                        );
                        this.dataSource.paginator = this.paginator;
                        this.dataSource.sort = this.sort;
                        this.snackBar.open(
                            MessageConstant.NoRecordFoundMessage,
                            "Dismiss",
                            { duration: 3000 }
                        );
                        return;
                    }
                },
                (err) => {}
            );
    }

    applyFilter(event: Event) {
        const filterValue = (event.target as HTMLInputElement).value;
        this.dataSource.filter = filterValue.trim().toLowerCase();

        if (this.dataSource.paginator) {
            this.dataSource.paginator.firstPage();
        }
    }

    openDialog(action, data): void {
        let params = {
            action: action,
            data: data,
        };
        const dialogRef = this.dialog.open(ActionDialogComponent, {
            width: "1200px",
            data: params,
            panelClass: "app-action-dialog",
            autoFocus: false,
            disableClose: true,
        });
        dialogRef.afterClosed().subscribe((result) => {
            setTimeout(() => {
                this.getRfidRecharge(this.selectedCpo_id);
            }, 1000);
        });
    }

    changeVisibility(visibleOption, ev) {
        ev.stopPropagation();
        if (visibleOption == "clientNameSearch") {
            this.clientNameSearch = !this.clientNameSearch;
            this.filterEntity.client_name = "";
        }
        if (visibleOption == "cpoSearch") {
            this.cpoSearch = !this.cpoSearch;
            this.filterEntity.cpo_name = "";
        }
        if (visibleOption == "stationNameSearch") {
            this.stationNameSearch = !this.stationNameSearch;
            this.filterEntity.station_name = "";
        }
        if (visibleOption == "stationNameSearch") {
            this.stationNameSearch = !this.stationNameSearch;
            this.filterEntity.station_name = "";
        }
        if (visibleOption == "rfidNoSearch") {
            this.rfidNoSearch = !this.rfidNoSearch;
            this.filterEntity.rf_id_no = "";
        }
        if (visibleOption == "balanceSearch") {
            this.balanceSearch = !this.balanceSearch;
            this.filterEntity.balance = "";
        }
        // if (visibleOption == "chargAmountSearch") {
        //     this.chargAmountSearch = !this.chargAmountSearch;
        //     this.filterEntity.charging_amt = "";
        // }
    }

    tableExport() {
        this.exportedData = [];
        for (let i = 0; i < this.dataSource.filteredData.length; i++) {
            var rfidRechargeModel = this.dataSource.filteredData[i];
            //this.exportedData.push({name: `name${i}`, description: `description${i}`,
            //statusString: `statusString${i}`});
            var data = {
                "Client Name": rfidRechargeModel.client_name,
                "CPO Name": rfidRechargeModel.cpo_name,
                "Station Name": rfidRechargeModel.station_name,
                "RFID No": rfidRechargeModel.rf_id_no,
                "Balance": rfidRechargeModel.balance,
            };
            this.exportedData.push(data);
        }
        const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
        const workBook: XLSX.WorkBook = XLSX.utils.book_new();

        XLSX.utils.book_append_sheet(workBook, workSheet, "sales disptch");
        XLSX.writeFile(workBook, "RFID By Cpo.xlsx");
    }

    pdfExport() {
        var doc = new jsPDF({
            // set needed dimensions for any elemen
        });
        this.exportedData = [];
        for (let i = 0; i < this.dataSource.filteredData.length; i++) {
            var rfidRechargeModel = this.dataSource.filteredData[i];
            //this.exportedData.push({name: `name${i}`, description: `description${i}`,
            //statusString: `statusString${i}`});
            var data = [
                rfidRechargeModel.client_name,
                rfidRechargeModel.cpo_name,
                rfidRechargeModel.station_name,
                rfidRechargeModel.rf_id_no,
                rfidRechargeModel.balance,
            ];

            this.exportedData.push(data);
        } 
        
        var img = new Image();
        var imgRight = new Image();
        var src = "assets/images/logos/logo_sparkev_main.png";
        var srcSparkEV = "assets/images/logos/logo_SparkEV_black.png";
        imgRight.src = srcSparkEV;
        var createdDate = new Date().toLocaleString("en-GB");
        img.src = src;
        doc.setFontSize(11);
        doc.setTextColor(100);
        doc.setFontSize(18);

        (doc as any).autoTable({
            head: this.head,
            body: this.exportedData,
            theme: "grid",
            headStyles: { fontStyle: "majalla", fillColor: [11, 148, 68] },
            styles: {
                cellPadding: 1,
                fontSize: 8,
            },

            margin: { top: 22 },
            didDrawCell: (data) => {},
            didDrawPage: (data) => {
                doc.addImage(img, 15, 7, 15, 6);
                doc.addImage(imgRight, 170, 7, 25, 6);

                doc.text(
                    "Charging Station Report",
                    doc.internal.pageSize.getWidth() / 2,
                    11,
                    { align: "center" }
                );

                doc.setTextColor(211, 211, 211);
                doc.setFontSize(60);
                doc.setGState(doc.GState({ opacity: 0.6 }));
                doc.text("Confidential/Not for Public use", 25, 285, null, 60);
                doc.setGState(doc.GState({ opacity: 1 }));
                doc.setTextColor(100);
                doc.setFontSize(9);
                doc.text("Date:" + createdDate, 15, 18);

                doc.text("Generated By:" + this.localStorage.f_Name, 195, 18, {
                    align: "right",
                });
                doc = this.addWaterMark(doc);
            },
        });

        // below line for Open PDF document in new tab
        doc.output("dataurlnewwindow");

        // below line for Download PDF document
        doc.save("Charging Station report.pdf");
    }

    addWaterMark(doc) {
        var newpage = doc.getNumberOfPages();
        const i = doc.internal.getNumberOfPages();
        doc.setTextColor(100);
        doc.setFontSize(10);
        //Print Page 1 of 4 for example

        doc.text(
            "Page " + String(i) + " of " + String(newpage),
            210 - 20,
            297 - 10,
            null,
            null,
            "right"
        );
        return doc;
    }
}
