import { Routes } from "@angular/router";
import { AuthGuard } from "app/shared/interceptors/auth-gaurd";

import { ListComponent } from "../list/list.component";
import { RechargeHistoryComponent } from "../recharge-history/recharge-history.component";
import { EnergyConsumptionLogsComponent } from "../energy-consumption-logs/energy-consumption-logs.component";

export const TABS_DEMO_ROUTES: Routes = [
    { path: "list", component: ListComponent, canActivate: [AuthGuard] },
    {
        path: "recharge-history",
        component: RechargeHistoryComponent,
        canActivate: [AuthGuard],
    },
    {
        path: "energy-consumption-logs",
        component: EnergyConsumptionLogsComponent,
        canActivate: [AuthGuard],
    },
    { path: "", redirectTo: "list", pathMatch: "full" },
];
