import { DatePipe } from "@angular/common";
import {
    Component,
    Inject,
    OnInit,
    ViewChild,
    ViewEncapsulation,
} from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { fuseAnimations } from "@fuse/animations";
import { MtxDialogData } from "@ng-matero/extensions";
import { RfidService } from "app/shared/services/rfid/rfid.service";

@Component({
    selector: "app-action-dialog",
    templateUrl: "./action-dialog.component.html",
    styleUrls: ["./action-dialog.component.scss"],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations,
    providers: [DatePipe],
})
export class ActionDialogComponent implements OnInit {
    params: any;
    isEditMode = false;
    form: FormGroup;
    localStorage: any;
    status = false;

    @ViewChild("toggleElement") ref: any;

    constructor(
        private fb: FormBuilder,
        private rfidService: RfidService,
        private snackBar: MatSnackBar,
        private datePipe: DatePipe,
        public dialogRef: MatDialogRef<ActionDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: MtxDialogData
    ) {}

    ngOnInit() {
        this.initialize();
    }

    initialize() {
        this.localStorage = JSON.parse(localStorage.getItem("userDetails"));

        this.params = this.data;

        this.isEditMode = this.params.action == "CREATE" ? false : true;

        this.form = this.fb.group({
            amount: ["", [Validators.required, Validators.pattern(/^[0-9]*$/)]],
        });

        // if (this.isEditMode) {
        //     this.assignValues(this.params);
        // }
    }

    close() {
        this.dialogRef.close();
    }
    closeActionPanel() {
        this.dialogRef.close();
    }

    onYesClick() {
        if (this.form.valid) {
            let formValues = this.form.value;

            const params: any = {
                rfid: this.params.data.id,
                cpo_id: this.params.data.cpo_id,
                station_id: this.params.data.station_id,
                amount: formValues.amount,
                expiry_date: this.datePipe.transform(new Date(), "yyyy-MM-dd"),
                created_date: this.datePipe.transform(new Date(), "yyyy-MM-dd"),
                created_by: this.localStorage.userId,
                status: this.status == true ? "Y" : "N",
            };

            if (this.isEditMode) {
                (params.id = this.params.data.id),
                    (params.modify_by = this.localStorage.userId);
            } else {
                params.created_by = this.localStorage.userId;
            }

            if (this.isEditMode) {
                this.rfidService.updateBalance(params).subscribe(
                    (data: any) => {
                        this.snackBar.open(
                            "Record updated successfully!",
                            "Dismiss",
                            { duration: 5000 }
                        );
                        this.dialogRef.close();
                    },
                    (err) => {
                        this.snackBar.open("Please try again", "Dismiss", {
                            duration: 5000,
                        });
                    }
                );
            }

            this.dialogRef.close(params);
        }
        return;
    }
}
