import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { RfidRechargeCrudComponent } from "./rfid-recharge-crud/rfid-recharge-crud.component";
import { AuthGuard } from "app/shared/interceptors/auth-gaurd";
import { TABS_DEMO_ROUTES } from "./rfid-recharge-crud/routes";

const routes: Routes = [
    {
        path: "crud",
        component: RfidRechargeCrudComponent,
        canActivate: [AuthGuard],
        children: TABS_DEMO_ROUTES,
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class RfidRechargeRoutingModule {}
